import { create } from 'zustand'
import { API_BASE_URL } from '../config/api';

export const useChatStore = create((set, get) => ({
  channel: {
    wellingtonsilverio: [
      {
        user: "wellingtonsilverio",
        message: "Olá, tudo bem? Como posso ajudar você hoje?",
        timestamp: new Date().toISOString(),
      }
    ]
  },
  loading: false,
  error: null,

  addChannel: (channelId) => set((state) => ({
    channel: {
      ...state.channel,
      [channelId]: [...state.channel[channelId]],
    }
  })),

  addMessage: async (channelId, user, message) => {
    const state = get();
    const currentMessages = state.channel[channelId] || [];

    // Adicionar mensagem do usuário imediatamente
    set((state) => ({
      channel: {
        ...state.channel,
        [channelId]: [
          ...currentMessages,
          {
            user,
            message,
            timestamp: new Date().toISOString(),
          }
        ]
      },
      loading: true,
      error: null
    }));

    try {
      // Preparar mensagens para a API (formato OpenAI)
      const apiMessages = currentMessages.map(msg => ({
        role: msg.user === 'wellingtonsilverio' ? 'assistant' : 'user',
        content: msg.message
      }));

      // Adicionar a nova mensagem do usuário
      apiMessages.push({
        role: 'user',
        content: message
      });

      // Fazer requisição para a API com timeout
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 10000); // 10 segundos

      const response = await fetch(`${API_BASE_URL}/chat`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          messages: apiMessages
        }),
        signal: controller.signal
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();

      if (data.success) {
        // Adicionar resposta do bot
        set((state) => ({
          channel: {
            ...state.channel,
            [channelId]: [
              ...state.channel[channelId],
              {
                user: "wellingtonsilverio",
                message: data.data.response,
                timestamp: data.data.timestamp || new Date().toISOString(),
              }
            ]
          },
          loading: false,
          error: null
        }));
      } else {
        throw new Error(data.message || 'Erro na API');
      }
    } catch (error) {
      console.error('Erro ao enviar mensagem:', error);

      // Adicionar mensagem de erro do bot
      set((state) => ({
        channel: {
          ...state.channel,
          [channelId]: [
            ...state.channel[channelId],
            {
              user: "wellingtonsilverio",
              message: "Desculpe, tive um problema técnico. Pode tentar novamente? 😅",
              timestamp: new Date().toISOString(),
            }
          ]
        },
        loading: false,
        error: error.message
      }));
    }
  },

  // Função para verificar se a API está funcionando
  checkAPIHealth: async () => {
    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 5000); // 5 segundos

      const response = await fetch(`${API_BASE_URL}/health`, {
        signal: controller.signal
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        return false;
      }

      const data = await response.json();
      return data.success;
    } catch (error) {
      console.error('API não está respondendo:', error);
      return false;
    }
  },

  // Função para obter informações do bot
  getBotInfo: async () => {
    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 5000); // 5 segundos

      const response = await fetch(`${API_BASE_URL}/info`, {
        signal: controller.signal
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        return null;
      }

      const data = await response.json();
      return data.data;
    } catch (error) {
      console.error('Erro ao obter informações do bot:', error);
      return null;
    }
  }
}))